// in src/comments.js
import React from "react";
import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardHeader from "@material-ui/core/CardHeader";
import Avatar from "@material-ui/core/Avatar";
import Person from "@material-ui/icons/Person";
import {
  TextField,
  EditButton,
  List,
  //Create,
  SimpleForm,
  TextInput,
  // SimpleShowLayout,
  // ReferenceField,
  // Show,
  // ReferenceManyField,
  // Datagrid,
  // Responsive,
  // DisabledInput,
  // LongTextInput
  Edit
} from "react-admin";

const cardStyle = {
  width: 300,
  minHeight: 300,
  margin: "0.5em",
  display: "inline-block",
  verticalAlign: "top"
};

export const Grid = ({ ids, data, basePath }) => (
  <div style={{ margin: "1em" }}>
    {ids.map(id => (
      <Card key={id} style={cardStyle}>
        <CardHeader title="My Account" avatar={<Avatar icon={<Person />} />} />
        <CardContent>
          <h5>
            Nama :
            <TextField title="Nama Pemilik" record={data[id]} source="nama" />
          </h5>
          <h5>
            Nama Bank:
            <TextField record={data[id]} source="namabank" />
          </h5>
          <h5>
            Alamat:
            <TextField record={data[id]} source="alamat" />
          </h5>
          <h5>
            No.HandPhone:
            <TextField record={data[id]} source="hp" />
          </h5>
          <h5>
            Total Pembelian:
            <TextField record={data[id]} source="totalpembelian" />
          </h5>
        </CardContent>
        <CardActions style={{ textAlign: "right" }}>
          <EditButton basePath={basePath} record={data[id]} />
        </CardActions>
      </Card>
    ))}
  </div>
);

export const AccountEdit = props => (
  <Edit {...props}>
    <SimpleForm>
      <TextInput source="nama" />
      <TextInput source="namabank" />
      <TextInput source="alamat" />
      <TextInput source="hp" />
      <TextInput source="totalpembelian" />
    </SimpleForm>
  </Edit>
);

export const MyaccountList = props => (
  <List title="My Account" {...props}>
    <Grid />
  </List>
);
