// in src/comments.js
import React from "react";
import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardHeader from "@material-ui/core/CardHeader";
import AccountBox from "@material-ui/icons/AccountBox";
import Avatar from "@material-ui/core/Avatar";
import {
  TextField,
  EditButton,
  List,
  ImageField,
  Create,
  SimpleForm,
  Edit,
  // ReferenceManyField,
  DisabledInput,
  // Datagrid,
  // DateField,
  // // ReferenceInput,
  // // SelectInput,
  // // Title,
  TextInput,
  LongTextInput
} from "react-admin";

const cardStyle = {
  width: 300,
  minHeight: 300,
  margin: "0.5em",
  display: "inline-block",
  verticalAlign: "center"
};

const CommentGrid = ({ ids, data, basePath }) => (
  <div style={{ margin: "0.5em" }}>
    {ids.map(accountId => (
      <Card key={accountId} style={cardStyle} title={""}>
        <CardHeader
          title="Update Harga"
          //subheader={<DateField record={data[accountId]} source="namabank" />}
          avatar={<Avatar icon={<AccountBox />} />}
        />
        <CardContent>
          <ImageField src="tampah-bank/tampah.png" />
          Nama Sampah :
          <TextField record={data[accountId]} source="namabarang" />
          Perkilo :<TextField record={data[accountId]} source="perkilo" />
          Harga :<TextField record={data[accountId]} source="harga" />
          Tanggal Update:
          <TextField record={data[accountId]} source="lastupdate" />
          {/* <ReferenceField
            label="AAAA"
            resource="comments"
            record={data[id]}
            source="post_id"
            reference="posts"
            basePath={basePath}
          >
            <TextField source="title" />
          </ReferenceField> */}
        </CardContent>
        <CardActions style={{ textAlign: "right" }}>
          <EditButton
            resource="myaccounts"
            basePath={basePath}
            record={data[accountId]}
          />
        </CardActions>
      </Card>
    ))}
  </div>
);

export const UpdateCreate = props => (
  <Create title="Tambah Penjualan Sampah" {...props}>
    <SimpleForm>
      {/* <ReferenceInput
        label="Jenis Barang"
        source="transactionId"
        reference="transactions"
      >
        <TextInput optionText="jenissampah" />
      </ReferenceInput>
      <TextInput source="title" />
      <LongTextInput source="body" /> */}
      <TextInput source="namabarang" />
      <TextInput source="perkilo" />
      <LongTextInput source="harga" />

      <LongTextInput source="lastupdate" />
    </SimpleForm>
  </Create>
);

export const UpdateEdit = props => (
  <Edit {...props}>
    <SimpleForm>
      <DisabledInput label="Id" source="updateId" />
      <TextInput source="namabarang" />
      <TextInput source="perkilo" />
      <TextInput source="harga" />
      <TextInput source="lastupdate" />
    </SimpleForm>
  </Edit>
);

export const UpdatehargaList = props => (
  <List title="Update Harga Barang" {...props}>
    <CommentGrid />
  </List>
);
